.page-header {
  min-height: 100vw;
  color: white;
  width: 100%;

  @media(min-width: $desktop) {
    @include bg-retinization(url("../img/background-desktop1x.jpg"), ("../img/background-desktop2x.jpg"), top, 100%, auto);
  }
};

.page-header--nojs,
.page-header--opened-menu {
  @media(max-width: $tablet) {

    & .page-header__bottom {
      @include bg-retinization(url("../img/background-tablet1x.jpg"), ("../img/background-tablet2x.jpg"), -122px, 100%, auto)
    }

    & .page-header__logo {
      opacity: 0;
    }
  }

  @media(max-width: $mobile) {

    & .page-header__bottom {
      @include bg-retinization(url("../img/background-mobile1x.jpg"), ("../img/background-mobile2x.jpg"), 0, 100%, 100%)
    }
  }
}

.page-header--closed-menu {
  @media(max-width: $tablet) {
    @include bg-retinization(url("../img/background-tablet1x.jpg"), ("../img/background-tablet2x.jpg"), 0, 100vw, auto);

    @media(max-width: $mobile) {
      @include bg-retinization(url("../img/background-mobile1x.jpg"), ("../img/background-mobile2x.jpg"), 0, 100vw, auto);
    }

    & .main-nav__list {
      display: none;
    }

    & .page-header__toggle {
      display: block;
    }
  }
};

.page-header__wrapper {
  box-sizing: border-box;
  @include desktop-width;
  display: grid;
  grid-template-areas: "a a"
  "b b"
  "c c";
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  margin: 0 auto;

  @media(max-width: $tablet) {
    display: block;
    padding: 0;
  }
}

.page-header__bottom {
  order: 3;
  grid-area: c;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0 70px;
  margin-bottom: 240px;


  @media(max-width: $tablet) {
    display: block;
    padding: 0;
  }

  @media(max-width: $mobile) {
    width: 100%;
  }
}

.page-header__philosophy {
  width: 40%;

  @media(max-width: $tablet) {
    width: 100%;
    padding-top: 20px;
    text-align: center;
    margin-bottom: 100px;
  }

  @media(max-width: 930px) {
    width: 100%;
    padding-top: 20px;
    text-align: center;
    margin-bottom: 150px;
  }

  @media(max-width: $mobile) {
    margin-bottom: 70px;
  }

  @media(max-width: 358px) {
    margin-bottom: 148px;
  }
}

.page-header__heading {
  margin: 0;
  padding: 0;
  @include text-size(46px, 64px, 700);
  letter-spacing: 0.005em;
  margin-bottom: 24px;

  @media (max-width: $tablet) {
    margin: 0 auto;
    @include text-size(30px, 42px, 700);
    margin-bottom: 20px;
  }
}

.page-header__text {
  margin: 0;
  padding: 0;
  @include text-size(20px, 32px, 300);
  letter-spacing: -0.005em;

  @media (max-width: $tablet) {
    width: 60%;
    margin: 0 auto;
    @include text-size(18px, 32px, 300);
  }

  @media (max-width: $mobile) {
    width: 90%;
  }
}

.page-header__logo {
  display: inline-block;
  width: 70px;
  margin-left: 70px;
  grid-area: b;
  margin-top: 38px;
  margin-bottom: 25px;

  @media(max-width: $tablet) {
    margin-left: 20px;
    margin-top: 20px;
    z-index: 2;
  }
}

.page-header__toggle {
  margin-left: auto;

  @media(max-width: $tablet) {
    margin-top: -76px;
  }
}

.page-header__form {
  margin-top: -62px;
  text-align: center;
  @media (max-width: $tablet) {
    margin: 0 auto;
    text-align: center;
  }
}

.form__add {
  height: 51px;
  @include text-size(15px, 21px, 600);
  border-radius: 6px;
};
